<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid text-start">
        <p class="fs-6 m-2">
          {{ this.$route.meta.title }}
        </p>
        <div class="">
          <label>تلفن همراه (نام کاربری) :</label>
          <p class="fs-5">{{ formData.email }}</p>
        </div>
        <div v-if="error" class="alert alert-danger">
          {{ error }}
        </div>
        <form class="row">
          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="first_name"
                name="first_name"
                placeholder="آنوشا"
                v-model="formData.first_name"
              />
              <label for="first_name">نام *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="last_name"
                name="last_name"
                placeholder="آنوشا"
                v-model="formData.last_name"
              />
              <label for="last_name">نام خانوادگی *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="address"
                name="address"
                placeholder="نشانی"
                v-model="formData.address"
              />
              <label for="address">نشانی</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="postall"
                name="postall"
                placeholder="کدپستی"
                v-model="formData.postall"
              />
              <label for="postall">کدپستی</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="provinces"
                name="provinces"
                @change="changeCities($event)"
              >
                <option value=""></option>
                <option
                  v-for="item in provinces"
                  :key="item.id"
                  :value="item.id"
                  :selected="item.id == formData.provinces_id"
                >
                  {{ item.name }}
                </option>
              </select>
              <label for="provinces">استان</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="cities_id"
                name="cities_id"
                v-model="formData.cities_id"
              >
                <option value=""></option>
                <option
                  v-for="item in cities"
                  :key="item.id"
                  :value="item.id"
                  :selected="item.id == formData.cities_id"
                >
                  {{ item.name }}
                </option>
              </select>
              <label for="cities_id">شهر</label>
            </div>
          </div>
        </form>
        <!-- <div>
          <a
            v-if="showSaveBtn"
            class="btn btn-primary btn-block"
            @click="editUser()"
          >
            ثبت
          </a>
        </div> -->

        <div class="row mt-5">
          <div class="col-md-12 col-xs-12 my-2">تغییر رمز عبور</div>
          <div class="alert alert-danger" v-show="passwordError">
            {{ passwordError }}
          </div>
          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="password"
                class="form-control"
                placeholder="رمز عبور"
                v-model="passData.current_password"
              />
              <label for="password">رمز عبور فعلی *</label>
            </div>
          </div>
          <div class="col-md-6 col-xs-12"></div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="password"
                class="form-control"
                id="password"
                name="password"
                placeholder="رمز عبور جدید"
                v-model="passData.new_password"
              />
              <label for="password">رمز عبور جدید *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="password"
                class="form-control"
                id="password_confirmation"
                name="password_confirmation"
                placeholder="تکرار رمز عبور"
                v-model="passData.new_password_confirmation"
              />
              <label for="password_confirmation">تکرار رمز عبور جدید *</label>
            </div>
          </div>

          <div class="col-md-12 col-xs-12">
            <a class="btn btn-primary btn-block" @click="changePassword()">
              تغییر رمز عبور
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";

export default {
  name: "Profile",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      error: null,
      showSaveBtn: true,
      formData: {
        id: this.$route.params.userId ? this.$route.params.userId : null,
        first_name: null,
        last_name: null,
        email: null,
        referrer_users_id: null,
        address: null,
        postall: null,
        cities_id: null,
        groups_id: null,
        provinces_id: null,
      },
      passData: {
        current_password: null,
        new_password: null,
        new_password_confirmation: null,
      },
      cities: null,
      provinces: null,
      passwordError: null,
    };
  },
  methods: {
    getProfieInfo() {
      this.arefApi
        .getUserProfile()
        .then((resp) => {
          this.formData = resp.data;
          this.getCities();
        })
        .catch((err) => {
          this.utils.showErrors(this,err);
        });
    },
    changePassword() {
      if (!this.passwordValodation()) {
        return;
      }

      this.arefApi
        .changePassword(this.passData)
        .then(() => {
          this.utils.showSuccess(this);
        })
        .catch((err) => {
          this.utils.showErrors(this,err);
        });
    },
    getCities: async function () {
      this.cities = await this.arefApi.getCities(true);
    },
    getProvinces: async function () {
      const res = await this.arefApi.getProvinces(true);
      this.provinces = res.data;
    },
    changeCities: async function (event) {
      const res = await this.arefApi.GetCitiesOfAProvince(
        event.target.value,
        true
      );
      this.cities = res.data;
    },
    passwordValodation() {
      this.passwordError = null;
      if (!this.passData.current_password) {
        this.passwordError = "رمز عبور فعلی را وارد کنید";
        return false;
      }

      if (this.passData.current_password.length < 6) {
        this.passwordError = "رمز عبور فعلی را صحیح وارد کنید";
        return false;
      }

      if (this.passData.new_password.length < 6) {
        this.passwordError = "رمز عبور جدید باید بیش از 6 کاراکتر باشد";
        return false;
      }

      if (this.passData.new_password_confirmation.length < 6) {
        this.passwordError = "تکرار رمز عبور جدید باید بیش از 6 کاراکتر باشد";
        return false;
      }

      if (this.passData.new_password != this.passData.new_password_confirmation) {
        this.passwordError = "رمز عبور جدید با تکرار آن برابر نیست";
        return false;
      }

      return true;
    },
  },
  mounted() {
    this.getProfieInfo();
    this.getProvinces();
  },
};
</script>

